import { useState } from "react";
import Button from "./Button";
import FilterCheckBox from "../component/FilterCheckBox";

const HospitalFilterModal = ({ checkValueHandle }) => {
    const [viewModal, setViewModal] = useState(true);
    const handleViewModal = () => {
        setViewModal(!viewModal);
    }
  
    const [dog, setDog] = useState(false);
    const [cat, setCat] = useState(false);
    const [hamster, setHamster] = useState(false);
    const [hadgehog, setHadgehog] = useState(false);
    const [guineaPig, setGuineaPig] = useState(false);
    const [turtle, setTurtle] = useState(false);
    const [ferret, setFerret] = useState(false);
    const [birds, setBirds] = useState(false);
    const [specialAni, setSpecialAni] = useState(false);
    const [parking, setParking] = useState(false);
    const [beauty, setBeauty] = useState(false);
    const [hotel, setHotel] = useState(false);
    const [allDay, setAllDay] = useState(false);
    const checkboxHandle = (id, check) => {
        checkValueHandle(id, check)
    }
    const changeDog = () => {
        setDog(!dog)
    }
    const changeCat = () => {
        setCat(!cat)
    }
    const changeHamster = () => {
        setHamster(!hamster)
    }
    const changeHadgehog = () => {
        setHadgehog(!hadgehog);
    }
    const changeGuineaPig = () => {
        setGuineaPig(!guineaPig);
    }
    const changeTurtle = () => {
        setTurtle(!turtle);
    }
    const changeFerret = () => {
        setFerret(!ferret);
    }
    const changeBirds = () => {
        setBirds(!birds);
    }
    const changeSpecialAni = () => {
        setSpecialAni(!specialAni);
    }
    const changeParking = () => {
        setParking(!parking);
    }
    const changeBeauty = () => {
        setBeauty(!beauty);
    }
    const changeHotel = () => {
        setHotel(!hotel);
    }
    const changeAllDay = () => {
        setAllDay(!allDay);
    }
    const changeAllCheckBox = () => {
        setDog(false);
        setCat(false);
        setHamster(false);
        setHadgehog(false);
        setGuineaPig(false);
        setTurtle(false);
        setFerret(false);
        setBirds(false);
        setSpecialAni(false);
        setParking(false);
        setBeauty(false);
        setHotel(false);
        setAllDay(false);
    } 

    if(viewModal === true) {
        return(
            <div className="HospitalFilterModalBtn" onClick={handleViewModal}>
                <span className="material-symbols-outlined">tune</span>
            </div>
        )
    } else {
        return(
            <div className="HospitalFilterModal">
                <div className="modalTop">
                    <span className="material-symbols-outlined closeModalBtn" onClick={handleViewModal}>arrow_back</span>
                    <h3> </h3>
                    <Button btnText={''} btnClick={changeAllCheckBox}/>
                </div>
                <div className="possPet">
                    <h5></h5>
                    <div>
                        <FilterCheckBox value={''} check={dog} 
                        onClick={()=>changeDog()} onChange={checkboxHandle}/>
                        <FilterCheckBox value={''} check={cat} onClick={()=>changeCat()} onChange={checkboxHandle} />
                        <FilterCheckBox value={''} check={hamster} onClick={()=>changeHamster()} onChange={checkboxHandle} />
                        <FilterCheckBox value={''} check={hadgehog} onClick={()=>changeHadgehog()} onChange={checkboxHandle} />
                        <FilterCheckBox value={''} check={guineaPig} onClick={()=>changeGuineaPig()} onChange={checkboxHandle} />
                        <FilterCheckBox value={''} check={turtle}  onClick={()=>changeTurtle()} onChange={checkboxHandle} />
                        <FilterCheckBox value={''} check={ferret}  onClick={()=>changeFerret()} onChange={checkboxHandle} />
                        <FilterCheckBox value={''} check={birds}  onClick={()=>changeBirds()} onChange={checkboxHandle} />
                        <FilterCheckBox value={''} check={specialAni} onClick={()=>changeSpecialAni()}  onChange={checkboxHandle} />
                    </div>
                </div>
                <div className="possService"> 
                    <h5></h5>
                    <div>
                        <FilterCheckBox value={''} check={parking} onClick={()=>changeParking()} onChange={checkboxHandle} />
                        <FilterCheckBox value={''} check={beauty} onClick={()=>changeBeauty()} onChange={checkboxHandle} />
                        <FilterCheckBox value={''} check={hotel} onClick={()=>changeHotel()} onChange={checkboxHandle} />
                        <FilterCheckBox value={'24'} check={allDay} onClick={()=>changeAllDay()} onChange={checkboxHandle} />
                    </div>
                </div>
                <p className="btnBox">
                    <Button btnText={' '} btnClick={handleViewModal} btnName={'submit'}/>
                </p>
            </div>
        )
    }
    

    
}

export default HospitalFilterModal;