import Button from "./Button";

import React from "react";

const ConsultBtns = ({ handleNumClick }) => {
    return (
        <div className="ConsultBtns">
            <div className="bt1">
                <Button btnName={'cateBtn'} btnText={''} btnClick={() => { handleNumClick(0) }} />
                <Button btnName={'cateBtn'} btnText={''} btnClick={() => { handleNumClick(1) }} />
                <Button btnName={'cateBtn'} btnText={''} btnClick={() => { handleNumClick(2) }} />
            </div>
            <div className="bt2">
                <Button btnName={'cateBtn'} btnText={''} btnClick={() => { handleNumClick(3) }} />
                <Button btnName={'cateBtn'} btnText={'/'} btnClick={() => { handleNumClick(4) }} />
                <Button btnName={'cateBtn'} btnText={''} btnClick={() => { handleNumClick(5) }} />
            </div>
        </div>
    );
}
export default ConsultBtns;