import { useContext, useState } from "react";
import { AnimalListDispatch } from "../App";
import moment from "moment";

import "../css/PetRecord.css";
import Radio from "./Radio";
import RadioGroup from "./RadioGroup";

const PetRecord = ({
    calValue,
    pet_id,
}) => {
    const { onSymptomAdd } = useContext(AnimalListDispatch);

    const [cough, setCough] = useState("1~3"); //
    const [runningNose, setRunningNose] = useState("");
    const [hunger, setHunger] = useState("");
    const [urine, setUrine] = useState("0~2");
    const [excrement, setExcrement] = useState("0");
    const [temperature, setTemperature] = useState("");
    const [active, setActive] = useState(" "); 
    const [misc, setMisc] = useState(""); //

    const handleMiscChange = (e) => {
        setMisc(e.target.value)
    }
    
    const handleSymptomAdd = () => {
        onSymptomAdd(pet_id, moment(calValue).format('YYYY/MM/DD'), cough, runningNose, hunger, urine,excrement, temperature, active, misc );
        window.confirm("  ?");
    };

    return (
        <div className="PetRecord">
            <RadioGroup
                className="cough"
                label={""}
                value={cough}
                onChange={setCough}
            >
                <Radio name={"cough"} value={"1~3"} defaultChecked>
                    1~3
                </Radio>
                <Radio name={"cough"} value={"4~6"}>
                    4~6
                </Radio>
                <Radio name={"cough"} value={"7 "}>
                    7 
                </Radio>
            </RadioGroup>
            <RadioGroup
                className={"runningNose"}
                label={""}
                value={runningNose}
                onChange={setRunningNose}
            >
                <Radio name={"runningNose"} value={""} defaultChecked>
                    
                </Radio>
                <Radio name={"runningNose"} value={""}>
                    
                </Radio>
                <Radio name={"runningNose"} value={""}>
                    
                </Radio>
            </RadioGroup>
            <RadioGroup
                className={"hunger"}
                label={""}
                value={hunger}
                onChange={setHunger}
            >
                <Radio name={"hunger"} value={""} defaultChecked>
                    
                </Radio>
                <Radio name={"hunger"} value={""}>
                    
                </Radio>
                <Radio name={"hunger"} value={""}>
                    
                </Radio>
            </RadioGroup>
            <RadioGroup
                className={"urine"}
                label={""}
                value={urine}
                onChange={setUrine}
            >
                <Radio name={"urine"} value={"0~2"} defaultChecked>
                    0~2
                </Radio>
                <Radio name={"urine"} value={"3~6"}>
                    3~6
                </Radio>
                <Radio name={"urine"} value={"7 "}>
                    7 
                </Radio>
            </RadioGroup>
            <RadioGroup
                className={"excrement"}
                label={""}
                value={excrement}
                onChange={setExcrement}
            >
                <Radio name={"excrement"} value={"0"} defaultChecked>
                    0
                </Radio>
                <Radio name={"excrement"} value={"1~2"}>
                    1~2
                </Radio>
                <Radio name={"excrement"} value={"3 "}>
                    3 
                </Radio>
            </RadioGroup>
            <RadioGroup
                className={"temperature"}
                label={""}
                value={temperature}
                onChange={setTemperature}
            >
                <Radio name={"temperature"} value={""} defaultChecked>
                    <br/>
                </Radio>
                <Radio name={"temperature"} value={""}>
                    
                </Radio>
                <Radio name={"temperature"} value={""}>
                    <br/>
                </Radio>
            </RadioGroup>
            <RadioGroup
                className={"active"}
                label={""}
                value={active}
                onChange={setActive}
            >
                <Radio name={"active"} value={" "} defaultChecked>
                     
                </Radio>
                <Radio name={"active"} value={" "}>
                     
                </Radio>
                <Radio name={"active"} value={""}>
                    
                </Radio>
            </RadioGroup>
            <div className="misc">
                <p></p>
                <input
                    type="text"
                    onChange={handleMiscChange}
                />
            </div>
            <div className="btnSave">
                <button
                    onClick={() => {
                        handleSymptomAdd();
                    }}
                >
                    
                </button>
            </div>
        </div>
    );
};
export default PetRecord;
